* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

}




html,
body {
	background-color: #DFE2CF;

}

.background {
	display: grid;
	grid-template: repeat(1, 1fr)/ 5fr 3fr;
	background-color: #1F363D;
	width: 100vw;
	height: 100vh;
	background-size: cover;
}

.contentbackground{
	background-color: #E9E3E6;
	height:100vh;
}

.SignupContent {
	width: 80%;
	background-color: #E9E3E6;
	border-radius: 15px;
	text-align: center;
	padding-top: 50px;
	padding-bottom: 50px
}

.Signupbox {
	display: flex;
	justify-content: center;
	align-items: center;
}

.SignupContent>span {
	font-family: Arial;
}

.alternatesignup {
	/* display: grid;
    grid-template-columns: repeat(16, 1fr); */
	margin-top: 30px;
	display: flex;
	justify-content: space-around;
}

.divider {
	width: 95%;
	margin-top: 20px;
	height: 100px;
	border-style: solid;
	margin-left: auto;
	margin-right: auto;
	background-color: black;
	opacity: 1;
}

.SignupContent [type=text],
input[type=password], input[type=email] {
	width: 90%;
	padding: 10px;
	margin: 20px 0;
	box-sizing: border-box;
	border-color: black;
	border-width: 1px;
	border-radius: 5px;
	box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.25);
}

.svg{
	height: 0px;
	width: 0px;
	padding: 0px;
	margin: 0px;
}

#Login-Button, #Register-Button{
	background-color: #000080;
	width: 90%;
	color: white;
	margin: 30px 0;
	padding: 15px;
	border-radius: 3px;
	border: 0;
	box-shadow: 0px 5px 5px -2px rgb(0, 0, 0, 0.25);
	cursor: pointer;
}

#Login-Button:hover, #Register-Button:hover {
	background-color: #03035f;
}

#facebookbutton {
	width: 180px;
	font-size: 15px;
	background-color: #4267B2;
	border-color: #4267B2;
	color: white;
	border-radius: 3px;
	border: none;
	box-shadow: 0px 5px 5px -2px rgb(0, 0, 0, 0.25);
}

#facebookbutton:hover {
	background-color: #36538f;
}

#googlebutton {
	width: 180px;
	height: 50px;
	font-size: 15px;
	background-color: #4285F4;
	border-color: #4285F4;
	color: white;
	border-radius: 3px;
	border: none;
	box-shadow: 0px 5px 5px -2px rgb(0, 0, 0, 0.25);
}

#googlebutton:hover {
	background-color: #3872ce;
}


#ErrMessage{
	color: red;
}

#Loader{
	width: 20px; 
	height: 20px;
}


/* //Loading Screen:  */

.loadScreen{
	background-color:#DFE2CF;
	height:29rem; 
	display: flex;
	justify-content: center;
	align-items: center;
}

.lds-ring {
	display: inline-block;
	position: relative;
	width: 100px;
	height: 100px;
  }
  .lds-ring div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 80px;
	height: 80px;
	margin: 8px;
	border: 8px solid blue;
	border-radius: 50%;
	animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: black transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
	animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
	animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
	animation-delay: -0.15s;
  }
  @keyframes lds-ring {
	0% {
	  transform: rotate(0deg);
	}
	100% {
	  transform: rotate(360deg);
	}
  }

/* NavBar CSS:  */
.top-nav{
	color: white;
	background-color:#E4572E;
	padding: 10px 40px 10px 70px;
	box-shadow: 0px 5px 5px -2px rgb(0, 0, 0, 0.25);
	position: sticky;
	z-index: 2;
	top: 0;
}

.top-nav ul{
	margin:0;
	padding: 0;
	display:flex; 
	list-style: none;
	flex-wrap: wrap;
	align-items: center;
	
}

.top-nav ul a.logo{
	flex:1;
	font-size: 30px;
	font-weight: 700;
	text-decoration: none;
	color: white;
}

.top-nav div.items{
	padding-left: 15px;
	display: inline-flex;
	

}

.top-nav div.search{
	display:flex;
	height: 40px;
	width: 240px;
	
}

.top-nav button{
	border:none;
	flex:1;
	border-radius: 0px 5px 5px 0px;


}

.top-nav button:hover{
background-color: #e6e6e6;

}



.top-nav ul .search input{
	width:200px;
	height: 100%;
	border:none; 
	outline: none;
	padding: 0px 10px;
	border-radius: 5px 0px 0px 5px;
}





nav ul div.items a{
	text-decoration: none;
	color: white;
	font-size: 18px;
	padding: 0 12px;
	
}

nav ul div.items a:hover{
	color: #d9d9d9;
}


/* productCatalogHeader */

.productShowCase{
	background-color: #1F363D;
	padding: 100px 0px;
	text-align: center;
	
	

}

.productShowCase text{
	font-size: 2.5rem;
	color: white;
	

}

.filter{
