	}






/* ProductPage CSS */

.productCatalog{
	background-color: #DFE2CF;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
	gap: 40px;
	padding: 40px 40px 40px 40px;
	
}


/* CSS for product Cards */

.productCard{
	width:350px;
	min-height: 430px;
	max-height: 430px;
	transition: 150ms;
	background-color: white;
	border-radius: 10px;
	margin: auto;
	padding:20px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	overflow: auto;
	box-shadow: 0px 0px 5px 2px rgb(0, 0, 0, 0.25);


}




.productCard img{

	max-width: 100%;
	max-height:200px;
}

.productCard:hover{
	transition: 150ms;
	transform: scale(1.02);
	

	
	
}



.CardContent{
	margin:25px 10px 25px 10px;
	font-size: 1.2rem;
	height:100px;

}



.productCard button{
	border: none;
	background-color: #1F363D;
	border-radius: 5px;
	color: white;
	padding: 10px 50px 10px 50px;

}

.productCard button:hover{
	background-color: #1a2d33;
}

/* Item Page CSS */


.ItemPage-inner{
	display: flex;
	height: 50rem;
	background-color: white;



}



	.ItemPage-inner div h2, .ItemPage-inner div h4{
		text-align: center;
	}
	.ItemPage-inner div h2{
		margin-bottom: 30px;
		margin-top: 30px;
		margin-left: 20px;
		margin-right: 20px;

		
	}
	.ItemPage-inner div h4{
		margin: 80px 0px 20px 0px;
		font-weight: normal;
	}
	.ItemPage-price-side{
			flex: 1;
			background-color: #F7F7F7;
			min-width: 600px;
	}

	.imgSide{
		flex: 1;
		min-width: 600px;

		
	}

	.img{
		width: 80%;
		height: 70%;
		margin: 90px auto;
		
	
	}

	.img img{
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: auto;
	}

		




/* Price List Table CSS */


.PriceTable{
	border-collapse: collapse;
	background-color: white;
	border-radius: 2px;
	margin: 25px auto 25px auto;
	font-size: 1.1rem;
	min-width: 400px;
	text-align: center;
	overflow: hidden;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
}

.PriceTable thead tr{
	background-color: #1F363D;
	color: white;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
}

.PriceTable tbody td{
	border-bottom: 1px solid lightgray;
}

.PriceTable th, .PriceTable td{
	padding: 15px 25px;
}

.PriceTable td a{
	margin-left: auto;
	margin-right: auto;
	width: 50px;
	height: 30px;

}

.PriceTable a img{
	width: 100%;
}

.test{
	display: block;
}

.test img{
	width: 100%;
	height: auto;
}





